import Vue from 'vue'

import VeeValidate from 'vee-validate'

import zh_TW from 'vee-validate/dist/locale/zh_TW'

Vue.use(VeeValidate)

VeeValidate.Validator.localize('zh_TW',{
    messages:{
        ...zh_TW.messages,
    },
    attributes:{
        veeEmail:'',
        veePwd:'',
        veeRepwd:'',
        veebox:''
    }
})

// 
VeeValidate.Validator.extend('agree',{
    validate:value => value,
    getMessage:field => field + ''
})